@charset "UTF-8";
@import "~/node_modules/normalize.css/normalize.css";
.assistive-text {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

html {
  font-size: 16px;
  line-height: 1.75;
  font-family: "Montserrat", sans-serif;
}
@media screen and (min-width: 75.6875em) {
  html {
    font-size: calc(1.02564vw + 3.59px);
  }
}
@media screen and (min-width: 100.0625em) {
  html {
    font-size: 20px;
  }
}

header {
  padding-top: 3.125em;
  padding-bottom: 3.125em;
  background-color: #181d27;
  color: #fff;
  font-family: "Raleway", sans-serif;
}
header a {
  color: inherit;
  opacity: 0.8;
}
header a:focus, header a:hover {
  opacity: 0.99;
}
header > .i {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.25em;
  max-width: 73.125em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header img.face {
  order: 2;
  vertical-align: middle;
  overflow: hidden;
  border: 0.25em solid #fff;
  border-radius: 50%;
  width: 33.333%;
}
header h1 {
  font-size: 300%;
  line-height: 1.0834333333;
  margin: 0 0 0 0;
}
header .position {
  display: flex;
  flex-wrap: wrap;
  font-size: 125%;
}
header .position span:after {
  content: "â€¢";
  margin: 0 0.25em;
}
header .position span:last-child:after {
  content: "";
  display: none;
}
header .home {
  font-size: 125%;
  font-style: italic;
}
header .contact {
  margin-top: 0.75em;
}
header .contact .method {
  margin-top: 0.5em;
}
header .contact .method a {
  color: inherit;
  text-decoration: none;
}
header .contact .method-proper {
  display: flex;
  align-items: center;
}
header .contact .method-proper .icon {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 50%;
  padding: 0.375em;
  width: 2em;
  height: 2em;
}
header .contact .method-proper .icon svg {
  width: 1.25em;
  height: 1.25em;
}
header .contact .method-proper .icon svg path {
  fill: #181d27;
}
header .contact .method-proper .value {
  font-size: 125%;
  line-height: 1.0001;
  margin: -0.15em 0 0 0.5em;
}
@media screen and (max-width: 47.9375em) {
  header > .i {
    flex-direction: column;
  }
  header img.face {
    order: 0;
    width: 15.625em;
  }
  header .info {
    margin-top: 0.75em;
    text-align: center;
  }
  header .contact {
    display: inline-block;
  }
}
@media screen and (max-width: 33.0625em) {
  header .position {
    font-size: 125%;
    line-height: 1.4001;
    display: block;
  }
  header .position span:nth-child(2):after {
    display: none;
  }
  header .position span:last-child {
    display: block;
  }
}

.bio-container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.25em;
  max-width: 73.125em;
  margin: 2.5em auto;
  max-width: 60em;
}
@media screen and (min-width: 75.0625em) {
  .bio-container {
    margin: 3.3333333333vw auto;
  }
}
.bio-container a {
  color: #4768b3;
  transition: all 300ms;
}
.bio-container a:focus, .bio-container a:hover {
  background-color: #f2f5ff;
}
.bio-container .extra {
  margin-top: 2.5em;
  border-top: 2px solid #996B00;
  padding-top: 1.25em;
}
.bio-container strong {
  font-weight: 600;
}
.bio-container h3, .bio-container h4, .bio-container h5 {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}
.bio-container h3 {
  font-size: 175%;
  margin: 1.0714285714em 0 0.7142857143em 0;
  color: #996B00;
}
.bio-container h4 {
  font-size: 150%;
  margin: 1.6666666667em 0 0.6666666667em 0;
}
.bio-container .plain-list {
  padding: 0;
}
.bio-container .plain-list li {
  list-style-type: none;
}
.bio-container .sourced-list li {
  margin-top: 1em;
}
.bio-container .sourced-list .what {
  font-weight: 600;
}
.bio-container .sourced-list .source {
  text-transform: uppercase;
  color: #767676;
}

@media screen and (max-width: 54.3125em) {
  .bio {
    font-size: 1.8390804598vw;
  }
}
@media screen and (max-width: 47.9375em) {
  .bio {
    font-size: 87.5%;
  }
}

.big {
  font-size: 150%;
  line-height: 1.5001;
}
@media screen and (max-width: 29.9375em) {
  .big {
    font-size: 125%;
    line-height: 1.5001;
  }
}

.first {
  margin-top: 2.5em;
}
.first:first-line {
  text-transform: uppercase;
}

.as-of {
  font-size: 93.75%;
  margin-top: 3.3333333333em;
}